<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <div class="container-fluid">
      <a class="navbar-brand" @click.prevent>Gestão de Senhas</a>
      <button aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"
              class="navbar-toggler" data-bs-target="#navbarSupportedContent"
              data-bs-toggle="collapse" type="button">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div id="navbarSupportedContent" class="collapse navbar-collapse">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item">
            <router-link class="nav-link" to="/inicio">
              <font-awesome-icon icon="home"/>
              Início
            </router-link>
          </li>
          <li v-if="showAdminBoard" class="nav-item">
            <router-link class="nav-link" to="/admin">Admin Board</router-link>
          </li>
          <li v-if="showModeratorBoard" class="nav-item">
            <router-link class="nav-link" to="/mod">Moderator Board</router-link>
          </li>
          <li class="nav-item">
            <router-link v-if="currentUser" class="nav-link" to="/user">
              <font-awesome-icon icon="user"/>
              Utilizador
            </router-link>
          </li>
        </ul>

        <ul v-if="!currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/registar">
              <font-awesome-icon icon="user-plus"/>
              Registar
            </router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/entrar">
              <font-awesome-icon icon="sign-in-alt"/>
              Entrar
            </router-link>
          </li>
        </ul>

        <ul v-if="currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/profile">
              <font-awesome-icon icon="user"/>
              {{ currentUser.username }}
            </router-link>
          </li>
          <li class="nav-item">
            <a class="nav-link" href @click.prevent="logOut">
              <font-awesome-icon icon="sign-out-alt"/>
              Sair
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "Header",
  computed: {
    currentUser() {
      console.log(this.$store.state.auth.user)
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_ADMIN');
      }

      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_MODERATOR');
      }

      return false;
    }
  },
  methods: {
    logOut() {
      this.$store.dispatch('auth/logout');
      this.$router.push('/entrar');
    }
  }
};
</script>

