<template>
  <div class="col-md-4 offset-md-4 text-center pt-4">
    <div class="jumbotron">
      <img
          id="profile-img"
          class="profile-img-card"
          src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
      />
      <form name="form" @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username">Nome de Utilizador</label>
          <input
              id="username"
              v-model="user.username"
              v-validate="'required'"
              class="form-control"
              name="username"
              type="text"
          />
          <div
              v-if="errors.has('username')"
              class="alert alert-danger"
              role="alert"
          >Por favor insira um nome de utilizador.
          </div>
        </div>
        <div class="form-group">
          <label for="password">Palavra-passe</label>
          <input
              id="password"
              v-model="user.password"
              v-validate="'required'"
              class="form-control"
              name="password"
              type="password"
          />
          <div
              v-if="errors.has('password')"
              class="alert alert-danger"
              role="alert"
          >Por favor insira uma palavra-passe.
          </div>
        </div>
        <div class="form-group">
          <button :disabled="loading" class="btn btn-primary btn-block">
            <span v-show="loading" class="spinner-border spinner-border-sm"></span>
            <span>Login</span>
          </button>
        </div>
        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">{{ message }}</div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import User from '../models/user';

export default {
  name: 'Login',
  data() {
    return {
      user: new User('', ''),
      loading: false,
      message: ''
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  created() {
    if (this.loggedIn) {
      this.$router.push('/profile');
    }
  },
  methods: {
    handleLogin() {
      this.loading = true;
      this.$validator.validateAll().then(isValid => {
        if (!isValid) {
          this.loading = false;
          return;
        }

        if (this.user.username && this.user.password) {
          this.$store.dispatch('auth/login', this.user).then(
              () => {
                console.log("Logged!")
                this.$router.push('/inicio');
              },
              error => {
                this.loading = false;
                if (error.response.status === 401) {
                  this.message = "Nome de utilizador ou palavra-passe errados."
                }
              }
          );
        }
      });
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

</style>
