<template>
  <div class="col-md-4 offset-md-4 text-center pt-4">
    <div class="jumbotron">
      <img
          id="profile-img"
          class="profile-img-card"
          src="../assets/user.png"
      />
      <form name="form" @submit.prevent="handleRegister">
        <div v-if="!successful">
          <div class="form-group">
            <label for="first_name">Primeiro Nome</label>
            <input
                id="first_name"
                v-model="user.first_name"
                v-validate="'required|min:3|max:20'"
                class="form-control"
                name="first_name"
                type="text"
            />
            <div
                v-if="submitted && errors.has('username')"
                class="alert-danger"
            >Por favor insira um nome válido.
            </div>
            <label for="last_name">Apelido</label>
            <input
                id="last_name"
                v-model="user.last_name"
                v-validate="'required|min:3|max:20'"
                class="form-control"
                name="last_name"
                type="text"
            />
            <div
                v-if="submitted && errors.has('username')"
                class="alert-danger"
            >Por favor insira um apelido válido.
            </div>
          </div>
          <div class="form-group">
            <label for="username">Nome de Utilizador</label>
            <input
                id="username"
                v-model="user.username"
                v-validate="'required|min:3|max:20'"
                class="form-control"
                name="username"
                type="text"
            />
            <div
                v-if="submitted && errors.has('username')"
                class="alert-danger"
            >Por favor insira um nome de utilizador válido.
            </div>
          </div>
          <div class="form-group">
            <label for="password">Palavra-passe</label>
            <input
                id="password"
                v-model="user.password"
                v-validate="'required|min:6|max:40'"
                class="form-control"
                name="password"
                type="password"
            />
            <div
                v-if="submitted && errors.has('password')"
                class="alert-danger"
            >Por favor insira uma palavra-passe válida.
            </div>
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <input
                id="email"
                v-model="user.email"
                v-validate="'required|email|max:50'"
                class="form-control"
                name="email"
                type="email"
            />
            <div
                v-if="submitted && errors.has('email')"
                class="alert-danger"
            >Por favor insira um endereço de email válido.
            </div>
          </div>

          <div class="form-group">
            <button class="btn btn-primary btn-block">Registar</button>
          </div>
        </div>
      </form>

      <div
          v-if="message"
          :class="successful ? 'alert-success' : 'alert-danger'"
          class="alert"
      >{{ message }}
      </div>
    </div>
  </div>
</template>

<script>
import User from '../models/user';

export default {
  name: 'Register',
  data() {
    return {
      user: new User('', '', '', '', ''),
      submitted: false,
      successful: false,
      message: ''
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push('/profile');
    }
  },
  methods: {
    handleRegister() {
      this.message = '';
      this.submitted = true;
      this.$validator.validate().then(isValid => {
        if (isValid) {
          this.$store.dispatch('auth/register', this.user).then(
              () => {
                this.message = "Conta registada com sucesso, faça login aqui";
                this.successful = true;
              },
              error => {
                console.log(error)
                this.message = "Ocorreu um erro, por favor tente mais tarde."
                this.successful = false;
              }
          );
        }
      });
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>
